export function objectKeys<T extends object>(object: T): Array<keyof T> {
    return Object.keys(object) as Array<keyof T>;
};
export function objectToArray<TObject extends object, TEach>(object: TObject, converter: (item: TObject[keyof TObject], key: keyof TObject) => TEach): Array<TEach> {
    return objectKeys(object).map(key => converter(object[key], key));
}
export function filterNulls<TItem>(array: Array<TItem | undefined>): Array<TItem> {
    return array.filter((item) : item is TItem => item != undefined);
}