import { IModel, IModule } from "../model/models";
import { IViewModel } from "../viewModel/IViewModel";

export type AppItems = IViewModel["studio"]["items"][number];
export type AppItemsWithPath = AppItems & { path: string };
export type IFlattenMapper<MappedType> = (item: AppItems) => MappedType;
export type ITraverseResult = "stop" | "continue";
export interface IModelTraverser {
    filter<Item extends IModule | IModel>(items: Array<Item>, predicate: (item: AppItems) => boolean): Array<Item>;
    flattenMapAll<MappedType>(mapper: IFlattenMapper<MappedType>): Array<MappedType>;
    flattenMap<MappedType>(items: Array<AppItems>, mappers: IFlattenMapper<MappedType>): Array<MappedType>;
    traverseChildren(items: Array<(IModule | IModel)>, cb: (item: IModule | IModel, index: number, containerArray: Array<IModule | IModel>) => ITraverseResult): ITraverseResult;
    traverseAllItems(cb: (item: IModule | IModel, index: number, containerArray: Array<IModule | IModel>) => ITraverseResult): ITraverseResult;
}
export const IModelTraverser = Symbol.for("IModelTraverser");