type ValueOf<Type> = Type extends any ? Type[keyof Type] : never;
type FieldValueOf<Type extends typeof typeSafety, Field extends keyof Type[keyof Type]> = Type[keyof Type][Field]
export type EditorModelTypes = ValueOf<FieldValueOf<typeof EditorConsts, "modelKeys">>;
let typeSafety: Record<string, { modelKeys: Record<string, string> }>;
export const EditorConsts = typeSafety = {
    quick: {
        modelKeys: {
            self: "qjson",
        },
    },
    flow: {
        modelKeys: {
            design: "flow-design",
            runtime: "flow-runtime",
        },
    },
    workflow: {
        modelKeys: {
            design: "workflow-design",
            runtime: "workflow-runtime",
        },
    },
    // flowEntity: { //FLOW ENTITY IS GOING BYE BYE (perhaps 🤔)
    //     modelKeys: {
    //         single: "flow-entity",
    //     },
    // },
    dataTypeEditor: {
        modelKeys: {
            single: DataTypeModelTypeTag,
        },
    },
    serviceEditor: {
        modelKeys: {
            single: "restService",
        },
    },
    soapEditor: {
        modelKeys: {
            single: "soapService",
        }
    },
    enum: {
        modelKeys: {
            single: "enum",
        },
    },
} as const;

interface ModelVisualInfo {
    icon: string;
    color: string;
}
const cssJsTsYaml: ModelVisualInfo = { color: "#449DD1", icon: "mdi mdi-cog-outline" };
const jpegJpgPngSvgGif: ModelVisualInfo = { color: "#212121", icon: "mdi mdi-image-outline " }
const formatIcon: ModelVisualInfo = { color: "#212121", icon: "mdi mdi-format-font " }
const moduleVisual = { color: "#007C77", icon: "mdi mdi-puzzle" }
const modelVisuals: Record<ModelType | "folder", ModelVisualInfo | Error | ((additional: ModelAdditionals | undefined) => ModelVisualInfo)> = {
    qjson: (additionals) => {
        if (additionals && (additionals as IQJsonAdditionals).qjsonType == "quickComponent") {
            return { color: "#449DD1", icon: "mdi mdi-shape-outline" };
        }
        return { color: "#449DD1", icon: "mdi mdi-palette" };
    },
    css: { color: "#151233", icon: "mdi mdi-music-accidental-sharp" },
    js: cssJsTsYaml,
    ts: cssJsTsYaml,
    yaml: cssJsTsYaml,
    entityDesigner: { color: "#E9724C", icon: "mdi mdi-database" },
    bpmn: { color: "#007C77", icon: "mdi mdi-vector-polyline" },
    process: { color: "#007C77", icon: "mdi mdi-auto-fix" },
    flow: { color: "#007C77", icon: "mdi mdi-step-forward" },
    workflow: { color: "orange", icon: "mdi mdi-chart-sankey-variant" },
    restService: { color: "#616161", icon: "mdi mdi-code-tags" },
    soapService: { color: "#616161", icon: "mdi mdi-code-tags" },
    // flowEntity: { color: "blue", icon: "mdi mdi-step-forward" }, //FLOW ENTITY IS GOING BYE BYE (perhaps 🤔)
    namedComponent: { color: "#212121", icon: "mdi mdi-cube-outline" },
    step: new Error("unimplemented for namedComponent"),
    jpeg: jpegJpgPngSvgGif,
    jpg: jpegJpgPngSvgGif,
    png: jpegJpgPngSvgGif,
    svg: jpegJpgPngSvgGif,
    gif: jpegJpgPngSvgGif,
    woff: formatIcon,
    woff2: formatIcon,
    ttf: formatIcon,
    otf: formatIcon,
    lottie: jpegJpgPngSvgGif,
    appSettings: { color: "#151233", icon: "mdi mdi-cog" },
    json: new Error("unimplemented for json"),
    dataType: { color: "#007C77", icon: "mdi mdi-cube" },
    folder: { color: "#212121", icon: "mdi mdi-folder" },
    enum: { color: "red", icon: "mdi mdi-enum" }, // bir zahmet ben yazdım diye böyle bırakmayın
    erronous: { color: "red", icon: "mdi mdi-alert" }
}

export class ModelContants {
    public static getModelVisualInfo(modelType: ModelType | ModuleObjectType | "folder", modelAdditionals: ModelAdditionals | undefined): ModelVisualInfo {
        const retVal = modelType == "module" ? moduleVisual : modelVisuals[modelType];
        if (retVal instanceof Error) { throw retVal; }
        return typeof retVal == "function" ? retVal(modelAdditionals) : retVal;
    }
}